@import "master";

* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  padding: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  color: #5d5d5d;
  // font-family: "Myriad Pro", sans-serif;
  font-family: "Arial", sans-serif;
  background-color: #ede6d7;
}
.main {
  flex: 1 0 auto;
  padding-bottom: 80px;
}
.footer {
  flex-shrink: 0;
}

img {
  width: 100%;
}
a {
  color: #617844;
  transition: 0.5s;
  text-decoration: underline;
  &:hover {
    color: #617844;
    opacity: 0.8;
  }
}
hr {
  border-color: #b8b1a2;
}

.main-header {
  background-image: url(../assets/header-bg.png);
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  min-height: 390px;
  position: relative;
  &__label {
    position: absolute;
    left: 0;
    top: 80px;

    font-size: 0.8125em;
    margin-top: -10px;
  }
  &__phone {
    position: absolute;
    right: 0;
    top: 90px;

    font-size: 1.25em;
    color: #a19b5e;
  }
}
.logo {
  width: 230px;
}
.navbar-nav {
  font-family: "Myriad Pro", sans-serif;
  font-size: 1.5em;
  .nav-item {
    padding: 0 0.5rem;
    position: relative;
    &:not(:last-child) {
      &::after {
        content: "\2807";
        color: #848212;
        position: absolute;
        right: -15px;
        top: 3px;
      }
    }

    .nav-link {
      color: #1e1e1e;
      padding: 0;
      text-decoration: none;
    }

    &:not(:first-child) {
      padding-left: 18px;
    }
    &.active {
      .nav-link {
        color: #848212;
        position: relative;
        &::after {
          content: "";
          position: absolute;
          left: 0;
          bottom: 0;
          height: 3px;
          width: 100%;
          background-color: #848212;
        }
      }
    }
  }
}

.about-section {
  margin-top: -105px;
  &__gears-border {
    background-image: url("../assets/about-bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: top center;

    position: relative;
    &-header {
      position: absolute;
      top: -24px;
      left: 50%;
      transform: translateX(-50%);

      border-top-left-radius: 16px;
      border-top-right-radius: 16px;
      background-image: url("../assets/about-header-bg.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: top center;
      width: 340px;
      height: 41px;
      padding: 10px;
      text-align: center;
      * {
        font-size: 1.125em;
        color: #fff;
      }
    }
    &-inner {
      padding: 40px 50px;
      font-size: 0.8125em;
      color: #443c2a;
    }
    &-link {
      position: relative;
      color: #443c2a;
      padding-right: 12px;
      &:hover {
        color: #443c2a;
        opacity: 0.8;
      }
      &::after {
        content: "\002BC8";
        position: absolute;
        right: 0;
        top: 3px;
        font-size: 11px;
      }
    }
  }
}

.content-section {
  padding-top: 40px;
  &__negative {
    margin-top: -140px;
  }
}
.sidebar {
  background-image: url(../assets/sidebar-bg.png);
  background-color: rgba(#c9c1af, 0.3);
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  height: 100%;
  &__menu {
    padding: 0;
    list-style: none;
    &-item {
      position: relative;
      padding-left: 16px;
      &::before {
        position: absolute;
        top: 3px;
        left: 0;
        content: "\00281B";
        color: #617844;
      }
    }
  }
  &__title {
    font-size: 1.375rem;
    img {
      width: 34px;
    }
  }
  &__article {
    &-date {
      font-size: 0.6875em;
      color: #8b8752;
      margin-bottom: 8px;
    }
    &-header {
      display: flex;
      &-img {
        padding: 1px;
        border: 2px solid #8b8752;
        width: 40%;
        flex: 0 0 auto;
        height: 100%;
        margin-top: 2px;
      }
      &-title {
        margin-left: 4px;
        font-size: 1em;
      }
    }
    &-content {
      font-size: 0.75em;
      color: #433f36;
    }
  }
}

.page-title {
  &-wrapper {
    display: flex;
    align-items: center;
  }
  font-size: 1.5em;
  font-weight: bold;
  margin: 0;
}
.download-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #5e4a3a;
  font-size: 0.8125em;
  &:hover {
    color: #5e4a3a;
  }
  img {
    width: 54px;
  }
}

.table-header {
  margin: 0;
  border: 1px solid #8c877c;
  border-bottom: 0;
  background-color: #cdc4b1;
  padding: 6px 0;
}
.table {
  color: #5d5d5d;
  border-color: #8c877c;
  thead {
    th {
      border-color: #8c877c;
      border-width: 1px;
    }
  }
  th,
  td {
    border-color: #8c877c;
  }
}
.price-col {
  min-width: 78px;
}

.article {
  font-size: 0.875em;
  margin-bottom: 20px;
  &__date {
    color: #908c6a;
    font-size: 0.9375em;
  }
  &__img {
    padding: 2px;
    border: 2px solid #968c77;
  }
  &__tags {
    &-wrapper {
      background-color: #b8b1a2;
      padding: 6px 15px;
      display: flex;
      color: #59614b;
    }
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
    margin-left: 8px;

    &-item {
      &:not(:last-child) {
        margin-right: 4px;
        &::after {
          content: ",";
          margin-left: -4px;
        }
      }
      a {
        color: #575145;
      }
    }
  }
}

.pagination {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__prev,
  &__next {
    width: 33%;

    &-link {
      text-decoration: none;
      &:hover {
        text-decoration: none;
      }
    }
  }

  &__prev {
    &-link {
      .pagination__text {
        position: relative;
        padding-left: 14px;
        &::before {
          position: absolute;
          left: 0;
          content: "\0025C2";
        }
      }
    }
  }
  &__next {
    &-link {
      .pagination__text {
        text-align: right;
        position: relative;
        padding-right: 14px;
        &::before {
          position: absolute;
          right: 0;
          content: "\0025B8";
        }
      }
    }
  }

  &__text {
    margin-bottom: 4px;
    color: #50453e;
    text-decoration: underline;
  }
}
.article-preview {
  &__date {
    font-size: 0.6875em;
    margin-bottom: 4px;
  }
  &__header {
    display: flex;
    color: #617844;
    &-img {
      border: 2px solid #8b8752;
      padding: 1px;
      width: 40%;
      flex: 0 0 auto;
      height: 100%;
      margin-top: 2px;
    }
    &-title {
      margin-left: 4px;
      font-size: 1em;
      line-height: 20px;
      text-decoration: underline;
    }
  }
}

.relevant-articles {
  &__title {
    font-size: 1.375em;
  }
}

.main-footer {
  background-image: url(../assets/footer-bg.png);
  color: #dbd6cc;
  padding: 40px 0;
  font-size: 0.8125em;
  a {
    color: #e6d1a5;
  }
  p {
    margin: 0;
  }
}
.sitemap-icon {
  width: 13px;
}

.bb-0 {
  border-bottom: 0;
  tbody {
    tr {
      &:last-child {
        th,
        td {
          border-bottom: 0;
        }
      }
    }
  }
}
